import ApiLogin from '@/api/login'
import { getToken, setToken, removeToken }from '@/common/utils/auth'

const user = {
  state: {
    token: getToken(),
    name: '',
    avatar: '',
    roles: []
  },

  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_NAME: (state, name) => {
      state.name = name
    },
    SET_AVATAR: (state, avatar) => {
      state.avatar = avatar
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles
    }
  },
  actions: {
    // 登录
    Login({ commit }, userInfo) {
      const username = userInfo.username.trim()
      return new Promise((resolve, reject) => {
        ApiLogin.login(username, userInfo.password).then(response => {
          setToken(response.token)
          commit('SET_TOKEN', response.token)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    // 手机号码 验证登录
    async LoginWithPhone({ commit, state }, info) {
      let userInfo = await ApiLogin.loginWithPhone(info.mobileCode, info.phoneNumber, info.validate_token)
      if (userInfo.token) {
        setToken(userInfo.token)
        commit('SET_TOKEN', userInfo.token)
        commit('SET_NAME', userInfo.name)
        commit('SET_AVATAR', userInfo.avatar)
      }
      return userInfo;
    },
    // 账号密码验证登录
    async LoginWithPassword({ commit, state }, info) {
      let userInfo = await ApiLogin.loginWithPassword(info.userAccount, info.passWord, info.codeNumber)
      if (userInfo.token) {
        setToken(userInfo.token)
        commit('SET_TOKEN', userInfo.token)
        commit('SET_NAME', userInfo.name)
        commit('SET_AVATAR', userInfo.avatar)
      }
      return userInfo;
    },

    // 获取用户信息  --token
    async GetInfo({ commit, state }) {
      console.log(state.token);
      let userInfo = await ApiLogin.getInfo(state.token);
      if (userInfo  && userInfo.token) {
        setToken(userInfo.token)
        commit('SET_TOKEN', userInfo.token)
        commit('SET_NAME', userInfo.name)
        commit('SET_AVATAR', userInfo.avatar)
      }
      return userInfo;
    },

    // 登出
    async LogOut({commit,state}) {
      let res = await ApiLogin.logout(state.token);
      commit('SET_TOKEN', '')
      commit('SET_ROLES', [])
      commit('SET_AVATAR', '')
      removeToken()
      return res;
      // resolve()
      // return new Promise((resolve, reject) => {
      //   ApiLogin.logout(state.token).then(() => {
      //     commit('SET_TOKEN', '')
      //     commit('SET_ROLES', [])
      //     removeToken()
      //     resolve()
      //   }).catch(error => {
      //     reject(error)
      //   })
      // })
    }
  }
}

export default user



