import router from '@/router'
import store from '@/store'
// import NProgress from 'nprogress' // Progress 进度条
// import 'nprogress/nprogress.css'// Progress 进度条样式
// import { Message } from 'element-ui'
import { getToken } from '@/common/utils/auth' // 验权
import { whiteList } from '@/router'

router.beforeEach( async (to, from, next) => {
  // console.log(to.path);
  // console.log(store.getters.name, store.getters.name.length, store.getters.name.length === 0);
  // NProgress.start()
  // next();
  // 已经登录
  if (getToken()) {
    // if (to.path === '/login') {
    if (false) {
      next({ path: '/' })
      // NProgress.done() // if current page is dashboard will not trigger	afterEach hook, so manually handle it
    } else {
      if (store.getters.name.length === 0) {
        // no have 用户信息
        try{
          let res = await store.dispatch('GetInfo');
          console.log(res);
          next();
        } catch (error) {
          let res = await store.dispatch('LogOut');
          // Message.error(err || 'Verification failed, please login again')
          next({ path: '/' })
        }
      } else {
        //  have 用户信息
        next()
      }
    }
  } else {
    if (true) { //都不用登陆验证
    // if (whiteList.indexOf(to.path) !== -1) {  //白名单的直接通过
    // if (whiteList.indexOf(to.path) >=0) {  //必须验证的名单
      next()
    } else {
      next('/login')
      // NProgress.done()
    }
  }
})

router.afterEach(() => {
  // NProgress.done() // 结束Progress
})



