const getters = {
  // sidebar: state => state.app.sidebar,
  // device: state => state.app.device,

  token: state => state.user.token,
  avatar: state => state.user.avatar,
  name: state => state.user.name,
  roles: state => state.user.roles,

  singer: state => state.music.singer,
  playing: state => state.music.playing,
  currentIndex: state => state.music.currentIndex,
  currentSong: state => {
    // console.log(state.music.currentIndex);
    // console.log("-------")
    // console.log(state.music.playList[state.music.currentIndex])
    return state.music.playList[state.music.currentIndex] || {}
  },
  rank: state => state.music.rank,
  recommend: state => state.music.recommend,
  fullScreen: state => state.music.fullScreen,
  playList: state => state.music.playList,
  sequenceList: state => state.music.sequenceList,
  mode: state => state.music.mode,
  searchHistory: state => state.music.searchHistory,
  loveList: state => state.music.loveList,
  inputPos:state => state.music.inputPos
}
export default getters



