import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

// 不重定向白名单
export const whiteList = [
  '/', '/demo/a', '/login', '/home'
]
export const constantRouterMap = [
  { path: '/login', component: () => import('@/views/login/login'), name:'Login'},
  { path: '/404', component: () => import('@/views/404'), name:'404'},
  // { path: '/401', component: import('errorPage/401'), },

  { path: '/', redirect: '/recommend' },
  { path: '/demo', component: () => import('@/views/demo/demo') , name:'Demo' },
  { path: '/recommend', component: () => import('@/views/recommend/recommend') ,name:'recommend'},

  { path: '/singer', component: () => import('@/views/singer/singer') ,
    children: [
      {path: ':id', component:  () =>  import('@/views/singer/singerDetail') },
    ]
  },

  { path: '/rank', component: () => import('@/views/rank/rank') ,
    children: [
      {path: ':id', component:  () =>  import('@/views/rank/rankDetail') },
    ]
  },

  { path: '/search', component: () => import('@/views/search/search'),name:'search'},
  // { path: '/', component: () => import('@/views/Home/index'), name: 'Home'},
  // { path: '/demo/:id', component: () => import('@/views/Demo/index'), name: 'Demo' },
  // { path: '/demo', component: () => import('@/views/Demo/index'), name: 'Demo'},
  // { path: '/swiper', component: () => import('@/views/Demo/swiper'), name: 'DemoSwiper'},
  // { path: '/modal', component: () => import('@/views/Demo/modal'), name: 'DemoModal'},
  // { path: '/userInfo', component: () => import('@/views/userInfo'), name: 'UserInfo' },
  // { path: '/uploadfiles', component: () => import('@/views/demo/uploadFiles'), name: 'DemoUploadFiles' },
  // { path: '/layout', component: () => import('@/views/demo/layout'), name: 'Layout' },

  // tabDemo
  // { path: '/tab', component: () => import('@/views/demo/tabview/index'), name: 'Tab',
  //   children: [
  //     { path: 'a', component: () => import('@/views/demo/tabview/a'), name: 'tab_a' },
  //     { path: 'b', component: () => import('@/views/demo/tabview/b'), name: 'tab_b' },
  //     { path: 'c', component: () => import('@/views/demo/tabview/c'), name: 'tab_c' },
  //     { path: 'd', component: () => import('@/views/demo/tabview/d'), name: 'tab_d' },
  //     { path: 'e', component: () => import('@/views/demo/tabview/e'), name: 'tab_e' }
  //   ]
  // },
  // { path: '/scroll', component: () => import('@/views/demo/scroll/simple-scroll-demo'), name: 'scrollDemo' },
  // { path: '/scroll_singerlist', component: () => import('@/views/demo/scroll/scroll_singerlist'), name: 'scrollsingerlist' },
  // { path: '/lazyload', component: () => import('@/views/demo/lazyload'), name: 'lazyload' },


  // 404
  { path: '*', redirect: '/404' }
]

export default new Router({
  base:'musicapp',
  mode: 'history', //后端支持可开
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})



