import Mock from 'mockjs'
import { param2Obj } from '@/common/utils'
import img from '@/assets/getcaptchas.png'
// import img_cwt from '/static/cwt.jpg'
// import img_cwt from '@/assets/cwt.jpg'
// var img_cwt =require("/static/cwt.jpg")
var img_cwt =require('@/assets/cwt.jpg')
// require('@/assets/logo3y.png')
const userMap = {
  admin: {
    roles: ['admin'],
    token: 'admin',
    introduction: '我是超级管理员',
    // avatar: 'https://wpimg.wallstcn.com/f778738c-e4f8-4870-b634-56703b4acafe.gif',
    avatar: img_cwt,
    name: 'Super Admin'
  },
  editor: {
    roles: ['editor'],
    token: 'editor',
    introduction: '我是编辑',
    // avatar: 'https://wpimg.wallstcn.com/f778738c-e4f8-4870-b634-56703b4acafe.gif',
    avatar: img_cwt,
    name: 'Normal Editor'
  },
  user: {
    roles: ['user'],
    token: 'user',
    introduction: '我是用户',
    // avatar: 'https://wpimg.wallstcn.com/f778738c-e4f8-4870-b634-56703b4acafe.gif',
    avatar: img_cwt,
    name: 'Cai哒肥'
  }
}

const api = {
  // config  请求参数
  loginByUsername: config => {
    const { username } = JSON.parse(config.body)
    return userMap[username]
  },
  getUserInfo: config => {
    const { token } = param2Obj(config.url)
    if (userMap[token]) {
      return userMap[token]
    } else {
      return false
    }
  },

  logout: () => 'success',
  captchas: () => { return { img: img, code:1234 } },
  checkExsis: (num) => { console.log(num); return false ; },
  mobileCode: () => '123456',

  // 短信登
  loginWithPhone(a,b,c) {
    return userMap.user
  },
  loginWithPassword(a,b,c) {
    return userMap.user
  },
  getInfo(config) {
    const {token}= param2Obj(config.url)
    if(token){
      return userMap.user
    }
    return null;
  }

}
Mock.mock(/\/login\/captchas/, 'get', api.captchas)
Mock.mock(/\/login\/checkExsis/, 'get', api.captchas)
Mock.mock(/\/login\/mobileCode/, 'get', api.mobileCode)
Mock.mock(/\/login\/phone/, 'post', api.loginWithPhone)
Mock.mock(/\/login\/account/, 'post', api.loginWithPassword)
Mock.mock(/\/login\/getInfo/, 'get', api.getInfo)
Mock.mock(/\/login\/logout/, 'post', api.logout)

export default Mock



