import {
  playMode
} from '@/common/js/config';

const music = {
    state: {
      singer: {},
      playing: false,
      fullScreen: false,
      mode: 0,
      playList: [],
      rank: {},
      recommend: {},
      playList: [], //它可以包含三种播放状态（顺序、单曲、随机）
      sequenceList: [], //默认的播放列表（顺序播放）
      mode: playMode.sequence,
      currentIndex: -1,
      searchHistory: [],
      loveList: [],
      inputPos: false
    },

    mutations: {
      SET_SINGER: (state, payload) => {
        state.singer = payload
      },
      SET_FULL_SCREEN(state, flag) { //播放器全屏（true/false）
        state.fullScreen = flag
      },
      SET_PLAYING_STATE(state, flag) { //播放状态（true/false）
        state.playing = flag
      },
      SET_PLAYLIST(state, list = {}) { //播放队列
        state.playList = list
      },
      SET_SEQUENCE_LIST(state, list) { //默认播放列表
        state.sequenceList = list
      },
      SET_PLAY_MODE(state, mode = 0) { //播放模式（0/1/2）
        state.mode = mode
      },
      SET_CURRENT_INDEX(state, index) { //当前播放歌曲索引（index）
        state.currentIndex = index
      },
      SET_SEARCH_HISTORY(state, histroy) { //搜索列表
        state.searchHistroy = histroy
      },
      SET_LOVE_LIST(state, love) { //收藏列表
        state.loveList = love
      },
      SET_INPUT_POS(state, pos) { //收藏列表
        state.inputPos = pos
      },
      SET_RANK(state, rank) { //收藏列表
        state.rank = rank
      }

    },
    actions: {
      // 点击歌曲播放
      toPlay({
        commit,
        state
      }, {
        list,
        index = 0
      }) {
        commit('SET_PLAYING_STATE', true)
        commit('SET_FULL_SCREEN', true)
        commit('SET_CURRENT_INDEX', index)
        commit('SET_PLAYLIST', list)
        commit('SET_SEQUENCE_LIST', list)
      },
      // 点击搜索列表的歌曲，会将该歌曲添加到播放列表，也涉及多个mutations
      // 此时涉及到三个state的修改
      searchToPlay({
        commit,
        state
      }, song) {
        let playList = state.playList.slice(0)
        let sequenceList = state.sequenceList.slice(0)
        let currentIndex = state.currentIndex
        let currentSong = playList[currentIndex]

        let in_playList_index = findIndex(playList, song) //这首歌是否已经在当前播放列表，是则in_playList_index > -1

        currentIndex++ //这里需要写currentIndex++（而不是下一句写currentIndex + 1），原先列表为空时才不会bug

        playList.splice(currentIndex, 0, song) //插入这首歌先,然后判断怎么删除原来已在列表中的歌

        if (in_playList_index > -1) {
          if (currentIndex > in_playList_index) { //说明插入的歌曲在重复歌曲的后面
            playList.splice(in_playList_index, 1) //删除前面这首歌
            currentIndex-- //删除后数组长度-1
          } else {
            playList.splice(in_playList_index + 1, 1)
          }
        }

        let currentSequenceIndex = findIndex(sequenceList, currentSong) //临时变量

        let in_sequenceList_index = findIndex(sequenceList, song) //是否在当前列表

        sequenceList.splice(currentSequenceIndex + 1, 0, song)

        if (in_sequenceList_index > -1) {
          if (currentSequenceIndex > in_sequenceList_index) {
            sequenceList.splice(in_sequenceList_index, 1)
          } else {
            sequenceList.splice(in_sequenceList_index + 1, 1)
          }
        }

        commit("SET_PLAYING_STATE", true)
        commit("SET_FULL_SCREEN", true)
        commit("SET_CURRENT_INDEX", currentIndex)
        commit("SET_PLAYLIST", playList)
        commit("SET_SEQUENCE_LIST", sequenceList)


      }
    }
  }

    function findIndex(list, song) {
      return list.findIndex((item) => {
        return item.id === song.id
      })
    }


    export default music



