import request from '@/common/utils/request'

export default {
  login(username, password) {
    return request({
      url: '/user/login',
      method: 'post',
      data: {
        username,
        password
      }
    })
  },

  logout() {
    return request({
      url: '/login/logout',
      method: 'post'
    })
  },

  getcaptchas() {
    return request({
      url: '/login/captchas',
      method: 'get'
    })
  },

  checkExsis(data) {
    return request({
      url: '/login/checkExsis',
      method: 'get',
      params: { iphone: data }
    })
  },

  mobileCode(phoneNumber) {
    return request({
      url: '/login/mobileCode',
      method: 'get',
      params: { pNum: phoneNumber }
    })
  },

  // 短信登录
  loginWithPhone(mobileCode, phoneNumber, validate_token) {
    return request({
      url: '/login/phone',
      method: 'post',
      params: { mobileCode, phoneNumber, validate_token }
    })
  },

  // let config = {
  //   headers: {
  //     'Content-Type': 'multipart/form-data'
  //   }
  // }
  // loginWithPhone(mobileCode, phoneNumber, validate_token) {
  //   return request.get('/login/1phone', { params: { mobileCode, phoneNumber, validate_token }})
  // },
  // 密码登录
  loginWithPassword(userAccount, passWord, codeNumber) {
    return request({
      url: '/login/account',
      method: 'post',
      data: { userAccount, passWord, codeNumber }
    })
  },

  getInfo(token) {
    return request({
      url: '/login/getInfo',
      method: 'get',
      params: { token }
    })
  }
}



